.editable_tracklist-row.u-vertical_margins {

    &:not(.nganimate-fade_in):not(.nganimate-fade_out) {
        display: block;
    }

    .editable_tracklist-row-entry.draggable_item,
    .editable_tracklist-row-placeholder {
        border-radius: 10px;
    }

    .square_input.square_input--full_width.ac_input {
        border-radius: 10px;
        padding: 12px;
    }

    .add-song-as-next-container {
        margin: 10px 0 0 10px;
    }
}

.gb-save-button {
    position: absolute;
    background-color: #1cc674;
    border: none;
    bottom: 35px;
    right: 40px;
    width: 113px;
    height: 50px;
    font-family: "Programme";
    text-align: center;
    font-size: 25px;
    transition: transform 0.2s ease-in-out, box-shadow 0.3s ease-in-out;
    color: #000 !important;
    box-shadow: 0 0 0 rgba(0, 0, 0, 0);
    overflow: hidden;

    &:hover {
        cursor: pointer;
        transform: scale(1.05);
        box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
    }
}


.bio-icon,
.people-icon,
.release-date-icon {
    position: absolute;
    transform: scale(0.8);
    transition: all 0.2s ease-in-out;
    top: 5px;
    left: -60px;
}

.autolink-artwork-icon-container {
    position: absolute;
    top: 67px;
    right: 45px;
    width: 100px;
    height: 100px;
    transition: all ease-in-out 0.2s !important;

    .autolink-artwork-icon {
        transform: scale(1.1);
        transition: all 1s ease-in-out;
    }

    .autolink-artwork-title {
        position: absolute;
        top: 108px;
        right: 9px;
        font-size: 22px;
        color: #000000;
        line-height: 120%;
        text-align: center;
        transition: all 0.4s ease-in-out;
    }

    .artwork-images-stack {
        position: absolute;
        top: 50px;
        right: 122px;
        width: 115px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        transition: all 0.3s ease-in-out;

        .artwork-image-container {
            position: absolute;
            display: flex;
            width: 100%;
            transition: all 0.3s ease-in-out;

            .artwork-image {
                border-radius: 10px;
                border: none;
                transition: all 0.3s ease-in-out;
            }

            .overlay {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background-color: rgba(0, 0, 0, 0.0);
                border-radius: 10px;
                border: none;
                transition: all 0.3s ease-in-out;

                &:hover {
                    background-color: rgba(0, 0, 0, 0.2);
                }

                .x-button {
                    position: relative;
                    top: 115px;
                    left: 30.5px;
                    padding: 2px;
                    border: none;
                    color: #000 !important;
                    transition: all 0.3s ease-in-out;
                    cursor: pointer;
                    display: border;

                    &:hover {
                        cursor: pointer;
                    }
                }

                .v-button {
                    position: relative;
                    top: 115px;
                    left: 30.5px;
                    padding: 2px;
                    border: none;
                    color: #000 !important;
                    transition: all 0.3s ease-in-out;
                    cursor: pointer;
                    display: border;

                    &:hover {
                        cursor: pointer;
                    }
                }
            }
        }

        .error-container {
            position: absolute;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            background-color: #fc5753;
            border-radius: 10px;
            border: none;
            height: 110px;
            width: 110px;
            transition: all 0.3s ease-in-out;

            .error-text {
                color: #000 !important;
                text-align: center;
                transition: all 0.3s ease-in-out;
            }
        }
    }

    &:hover {
        cursor: pointer;
        background-color: #979797;

        .autolink-artwork-icon {
            transform: scale(1.4) rotate(360deg);
        }

        .autolink-artwork-title {
            transform: scale(1.05);
        }
    }
}

.extension-icon {
    transform: scale(0.6);
    position: absolute;
    bottom: -3px;
    right: -8px;
    cursor: pointer;
    transition: all 0.2s;

    &:hover {
        transform: scale(0.69);
    }

    &:focus {
        transform: scale(0.6);
    }
}

.blured-background {
    position: fixed;
    z-index: 999;
    inset: 0;
    width: 100vw;
    height: 100vh;
    border: none;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: center;
    justify-content: center;
    background: rgba(0, 0, 0, 0.8);
    backdrop-filter: blur(2px);

    .extension-box {
        position: absolute;
        width: 1200px;
        min-height: 700px;
        border-radius: 4px;
        padding: 20px;
        border: none;
        background: #fffe65;
        color: #000 !important;
        margin-left: auto;
        margin-right: auto;
        //overflow-y: scroll;
        overflow-x: hidden;

        -moz-user-select: none;
        -webkit-user-select: none;
        -ms-user-select: none;
        user-select: none;
        -o-user-select: none;

        .close-icon {
            position: absolute;
            right: 10px;
            top: 10px;
            cursor: pointer;
        }

        .title {
            position: relative;
            left: 65px;
            font-size: 30px;
            font-weight: bold;

            &.add-tags-title {
                top: 40px;
            }

            &.add-credits-title {
                top: 75px;
            }

            &.add-media-title {
                top: 110px;
            }
        }
    }
}

.gb-textarea {
    border: none;
    outline-style: solid;
    outline-width: 0;
    outline-color: #fc5753;
    resize: none;
    overflow: hidden;
    font-family: "Programme";
    font-size: 18px;
    font-weight: 500px;
    justify-content: center;
    writing-mode: horizontal-tb !important;
    column-count: initial !important;
    text-align: start;
    transition: all 0.2s !important;
    position: relative;
    color: #000 !important;

    &.add-tags {
        top: 50px;
        left: 65px;
        width: 500px;

        &:focus,
        &.tagify--focus {
            border-bottom-left-radius: 0;
            border-bottom-right-radius: 0;
        }
    }

    &.add-credits {
        top: 85px;
        width: 245px;

        &.role {
            left: 65px;
        }

        &.artist {
            left: 75px;
        }
    }

    &.add-media {
        top: 120px;
        left: 65px;
        width: 500px;
        transition: outline 0.05s ease-in-out, background-color 0.2s ease-in-out !important;

        &:not(.details) {
            z-index: 2
        }

        &.details {
            background-color: #f1f1f1;
            border-radius: 0 0 10px 10px;
            padding: 17.5px 5px 5px 5px;
            top: 110px;
            z-index: 1;
            cursor: default;

            &:not(.image-container):not(.image):not(.title):not(.artist) {
                height: 80px;
                width: 480px;
                left: 75px;
            }

            &:has(> .title[title=""]),
            &:has(> .title:not([title])) {
                height: 0 !important;
            }

            * {
                position: absolute;
            }

            .image-container {
                display: inline-flex;
                flex-direction: row;
                align-items: center;
                justify-content: flex-start;
                width: 100px !important;
                height: 56.25px !important;

                .image:not([src=""]) {
                    border-radius: 4px;
                    margin-right: 10px;
                    // make sure it will be in the height of the container, and keep the aspect ratio
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }

            .title {
                display: inline-block;
                font-family: "Programme";
                font-size: 18px;
                font-weight: 500;
                color: #000;
                max-width: 330px;
                max-height: 22px;
                text-align: left;
                left: 120px;
                top: 22px;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
            }

            .artist {
                display: inline-block;
                font-family: "Programme";
                font-size: 14px;
                font-weight: 500;
                color: rgba(0, 0, 0, 0.4);
                max-width: 340px;
                text-align: left;
                left: 120px;
                top: 47px;
            }

            .length {
                display: inline-block;
                font-family: "Programme";
                font-size: 16px;
                font-weight: 500;
                max-width: 360px;
                text-align: left;
                right: 5px;
                top: 33px;
            }
        }
    }

    &.error {
        outline-width: 2px !important;

        &.error-animation {
            background-color: #fc5753;
        }
    }

    &::placeholder {
        font-family: "Programme";
        font-size: 18px;
        color: rgba(0, 0, 0, 0.4) !important;
        transition: all 0.2s;
    }

    &:hover {
        cursor: pointer;
        background-color: #979797;

        &.add-credits {
            cursor: not-allowed !important;
        }
    }

    &:focus,
    &.tagify--focus {
        cursor: text;
        background-color: #cbcbcb;

        &::placeholder {
            color: rgba(0, 0, 0, 0.25) !important;
        }
    }
}

