.header_with_cover_art {
    border-radius: 20px;
    box-shadow: 0 0 8px 8px #f7f7f7 inset;

    &::before {
        border-radius: 20px;
        box-shadow: 0 0 4px 4px #f7f7f7 inset;
        backdrop-filter: blur(7.5px);
    }

    .header_with_cover_art-inner {
        background: linear-gradient(180deg, rgba(0, 0, 0, 0) 1%, rgba(0, 0, 0, 0.5) 2%, rgba(0, 0, 0, 0.3) 30%, rgba(0, 0, 0, 0) 75%);

        max-width: 1170px !important;
        width: auto !important;

        .header_with_cover_art-cover_art {
            .cover_art {
                border-radius: 10px !important;
                border-color: transparent !important;
                cursor: nwse-resize;

                &-cta {
                    display: none !important;
                }

                &:first-child {
                    box-shadow: 0 0 10px 10px #f7f7f7 inset;
                }
            }

            &:hover {
                .cover_art {
                    border-radius: 5px !important;

                    &-image {
                        transform: scale(1.025) !important;
                    }
                }
            }
        }

        .header_with_cover_art-primary_info_container {
            margin-top: 25px !important;

            .header_with_cover_art-primary_info {
                margin-left: 10px !important;

                >div {
                    &:nth-child(1) {
                        display: none !important;
                    }


                }

                >h1 {
                    font-size: 2.1em;
                    font-weight: 600;
                    text-align: left;
                    width: 450px !important;
                }

                >h2 a {
                    font-size: 1.3em;
                    font-weight: 300;
                    text-align: left;
                    max-width: 450px !important;
                    color: #ffff65 !important;
                    transition: all 0.05 cubic-bezier(0.25, 0.46, 0.45, 0.94) !important;

                    &:hover {
                        text-decoration: underline;
                        letter-spacing: 0.5px;
                    }
                }
            }
        }
    }
}

:not(.modal_window-content) .column_layout {
    &-column_span {
        &--primary {
            >div {
                width: 645px !important;

                >h2.text_label.text_label--gray.u-half_bottom_margin {
                    display: none !important;
                }

                album-tracklist-row {
                    .chart_row {
                        width: 625px !important;
                        padding: 16px !important;
                        border-color: transparent !important;
                        border-radius: 10px !important;
                        transition: width 0.2s cubic-bezier(0.25, 0.46, 0.45, 0.94), color 0.15s ease-in-out, background-color 0.2s ease-in-out !important;

                        a {
                            h3 {
                                font-weight: 400 !important;
                            }

                            &:hover {
                                padding: 0 !important;
                            }
                        }

                        &::before {
                            display: none !important;
                        }

                        &-content {
                            &-title-subtitle {
                                display: none !important;
                            }
                        }

                        &:hover {

                            // make sure there's no img child element hovered
                            &:not(:has(img:hover)) {
                                width: 645px !important;
                                background-color: #2c2c2c;
                                color: #e9e9e9 !important;

                                a h3 {
                                    color: #e9e9e9 !important;
                                    padding-left: 10px !important;
                                }
                            }
                        }
                    }
                }
            }
        }

        &--secondary {
            left: 105px !important;
        }
    }
}